export const camel2kebab = (str) => str.replace(/([a-z])([A-Z])/g, '$1-$2').toLowerCase();
export const roundUp = (num, digits = 2) => +num.toFixed(digits);
export const kebab2camel = (str) => str.replace(/-([a-z])/g, (a, b) => b.toUpperCase());
export const isAlmostSameNumber = (a, b) => Math.trunc(a) === Math.trunc(b);
export const isNaN = (num) => num !== num;
